 /* estilos del primer componentes */
 .bodyback{
    text-align: center;
    background-image: url("data:image/jpeg;base64,");
    background-position: center;
    width: 100%;
    height: 640px;
    background-repeat: no-repeat;
    background-size: cover;
 }

.image{
    text-align: center;
    margin-top: 10%;
    margin-bottom: 100%;
    width: 50%;
    background-size: cover;
}
.botones {
    margin-bottom : 10%;
}
.buttonMovil1 {
    margin: 1%;
    border-radius: 5px;
    text-decoration: none;
    font-size: 2em;
    font-family: 'arial', cursive;
    color: white;
    border: 2px solid rebeccapurple;
    background-color: transparent;
    padding: 3% 10%;
    display: inline-block;
}
.buttonMovil2 {
    margin: 1%;
    border-radius: 5px;
    font-family: arial;
    color: white;
    text-decoration: none;
    font-size: 2em;
    border: 2px solid rebeccapurple;
    background: -webkit-radial-gradient(#ca1291, #ad13ca);
    background: -moz-radial-gradient(#ca1291, #ad13ca);
    padding: 4% 10%;
}




